* {
  margin:0;
  padding:0;
  box-sizing:border-box;
}

html {
  font-family: "Open Sans", sans-serif;
  font-size:20px;
}
footer span {
    color:red;
}

footer p{
    font-size:14px;
}
header {
    position:relative;
    overflow:hidden;
    background-color:#333;
    
}

header img{
    position:absolute;
    /* top: 50%;
    left:50%;
    transform:translate(-50%,-50%); */
    min-height:100%;
    left:50%;
    transform:translateX(-50%);
    
}
nav .navigation {
    padding:0 30px 30px 0;
}

.navigation {
    list-style-type:none;
    cursor:pointer;
    
}

nav .navigation a{
    display:block;
    text-decoration:none;
    color:black;
    text-transform:uppercase;
    font-weight:bold;
    padding:10px;
    transition:0.2s;
}

nav .navigation a:hover{
    transform:translateX(20px);
}

nav .navigation a.active{
    color:red;
    border-left:6px solid red;
    /* transform:translateX(0px); */
}
nav .navigation a.active:hover{
    transform:translateX(0px);
}

nav .navigation li:nth-child(1) a.active{
    border-left-color: blue;
    color:blue;
}
nav .navigation li:nth-child(2) a.active{
    border-left-color: orange;
    color:orange;
}

nav .navigation li:nth-child(3) a.active{
    border-left-color: green;
    color:green;
}
.contact textarea{
    font-size:16px;
    width:80%;
    min-height:200px;
    margin:20px 0;
    line-height:1.5;
}

.contact button{
    margin-left:80%;
    transform:translateX(-100%);
    display:block;
    border:2px solid gray;
    color:gray;
    border-radius:5px;
    background-color:#fff;
    cursor:pointer;
    padding:15px 20px;
    font-size:20px;
    transition:0.3s;
}

.contact button:hover{
    color:black;
    border-color:black;
    
}
.products h3 {
    font-size:30px;
}

.products ul {
    list-style: square;
    padding-left: 40px;
}

.products li {
    margin:20px 0;
}

.products a{
    text-decoration:none;
    color:#333;
}

.products a:hover{
    text-decoration:underline;
    color:black;
}
div.app{
  display:flex;
  flex-direction:column;
  min-height:100vh;
  width:90%;
  
  margin:0 auto;
  color:#222;
}

header{
  flex-basis: 30vh;
  
}

main{
  padding:30px 0 ;
  
  flex-grow:1;
  display:flex;

}
aside{
  min-width:240px;
  /* background-color:#555; */
}

section.page{
  
  flex-grow:1;
}

footer{
  flex-basis: 6vh;
  min-height:6vh;
  background-color:black;
  color:white;
  padding:10px 0;
  text-align:center;
  text-transform:uppercase;
  /* bottom:0px; */
}
